body {
  margin: 0;
  padding: 0;
  font-family: 'Press Start 2P', cursive, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dimension-dialog {
  display: flex;
  width: 747px;
  height: 465px;
  border: 1px solid #000000;
  margin-top: 100px;
  flex-direction: column;
  align-items: center;
}

.dimension-dialog > h3 {
  margin-top: 47px;
  font-size: 24px;
}

.dimension-dialog > form {
  display: flex;
  flex-direction: column;
}

.dimension-inputs {
  display: flex;
  margin-top: 72px;
  width: 466.5px;
  justify-content: space-between;
}

.dimension-inputs > .input-group {
  display: flex;
  text-align: center;
  flex-direction: column;
  font-size: 14px;
}

.dimension-inputs > .input-group > input {
  width: 206px;
  height: 51px;
  border: 1px solid #000000;
  margin-top: 8px;
  font-size: 30px;
  font-family: inherit;
  padding: 0 10px;
  text-align: center;
  box-sizing: border-box;
  outline-color: #808080;
}

.start-game-btn {
  margin: auto;
  margin-top: 119px;
}

.cell {
  display: table-cell;
  width: 50px;
  height: 50px;
  border: 1px solid #000000;
  text-align: center;
  vertical-align: middle;
}


.row {
  display: table-row;
}

.grid {
  display: table;
  margin: auto;
  border-collapse: collapse;
}

.move-count {
  display: inline-block;
  border: 1px solid #000000;
  padding: 9px;
  margin: auto;
  margin-bottom: 25px;
}

.game-over {
  border: 1px solid #000000;
  padding: 24px;
  margin-top: 100px;
}

.game-over > h4 {
  margin-bottom: 86px;
}
.game-area {
  text-align: center;
}

.game-area > .instruction {
  font-size: 18px;
}


.app-header {
  text-align: center;
}

.app-header > h1 {
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 42px;
  margin-top: 23px;
  margin-bottom: 0;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  width: 234px;
  height: 61px;
  color: white;
  font-family: inherit;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 18px;
  cursor: pointer;
  outline-color: #808080;
  border: 0;
}

.primary-btn {
  background: #000000;
  color: #FFFFFF;
}

.primary-btn:hover {
  border: 1px solid #000000;
  background: #FFFFFF;
  color: #000000;
}

.secondary-btn {
  border: 1px solid #000000;
  background: #FFFFFF;
  color: #000000;
}

.secondary-btn:hover {
  background: #000000;
  color: #FFFFFF;
}

.game-btn {
  height: auto;
  font-size: 14px;
  width: 185px;
  margin: 0px 18px;
  padding: 8px;
}

.game-over-btn {
  width: 233px;
  margin: 0px 20px;
}
